 html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}


body {
  color: #ffffff;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
  overflow-x: hidden;
}

a {
  text-decoration: none
}

img {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}


