.travelmates {
  position: relative;
  background-color: #192144;
  border-radius: 0 0 30px 30px;
  z-index: 4;
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0 25px;
    margin: 0 auto;
    margin-top: -27px;
    width: 320px;
    background-image: url("../img/smolov-back-desktop@1x.png"),url("../img/rogovaya-back-mobile@1x.png");
    background-repeat: no-repeat,no-repeat;
    background-position: 152px 66px,-10px 338px;
    &::before {
     content: "";
     position: absolute;
     top: 66px;
     left: -19px;
     width: 132px;
     height: 132px;
     border-radius: 20px;
     background-color: #141b3b;
     z-index: -1;
   }
 }
}

.travelmates__title {
  margin-top: 99px;
  margin-left: 24px;
  color: #ff8d30;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.1px;
}

.travelmates__text {
  width: 216px;
  margin-top: -1px;
  margin-left: 25px;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
}

.travelmates__text--bold {
  margin-top: 5px;
  margin-bottom: 53px;
  font-weight: 800;
}

.travelmates__item-wrapper {
  margin-top: 1px;
  position: relative;
}

.travelmates__item {
  position: relative;
  margin-top: 1px;
}

.travelmates__item--levelup {
 position: absolute;
 top: 16px;
 left: 16px;
 width: 60px;
 height: 60px;
 background-color: rgba(0,0,0,0.6);
 border-radius: 50%;
 z-index: 0;
 border: 3px solid #fe8d30;
}

.travelmates__item--number {
  position: absolute;
  top: 10px;
  left: 15px;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
}

.travelmates__item--level {
 position: absolute;
 top: 35px;
 left: 16px;
 font-size: 14px;
 font-weight: 400;
 line-height: 14px;
}
.travelmates__item--level::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -4px;
  width: 31px;
  height: 1px;
  opacity: 0.3;
  background-color: white;
}

.travelmates__item--bg {
  border-radius: 10px 10px 0 0;
}

.travelmates__item--description {
  position: relative;
  width: 270px;
  height: 157px;
  margin-top: -7px;
  margin-bottom: 8px;
  background-color: #ffffff;
  border-radius: 0 0 10px 10px;
}

.travelmates__online {
  position: absolute;
  top: 34px;
  left: 63px;
  width: 5px;
  height: 5px;
  border-radius: 4px;
  background-color: #81e31e;
}

.travelmates__offline {
  position: absolute;
  top: 33px;
  left: 53px;
  width: 5px;
  height: 5px;;
  border-radius: 4px;
  background-color: #f97676;

}

.travelmates__item--countries {
  position: absolute;
  display: flex;
  padding-right: 1px;
  justify-content: center;
  width: 143px;
  top: -14px;
  left: 54px;
}

.travelmates__item--countrie {
  margin-left: 19px;
  width: 35px;
  height: 24px;
}

.travelmates__item--name {
  color: #1d2e5b;
  padding-top: 27px;
  text-align: center;
  font-size: 21px;
  font-weight: 700;
  line-height: 21px;
}

.travelmates__item--tags {
  width: 180px;
  text-align: center;
  margin-top: -7px;
  margin-left: 49px;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
}

.travelmates__item--tags a {
  color: #444444;
  text-decoration: none;
}

.travelmates__icons {
  display: flex;
  position: relative;
  margin-top: 6px;
  justify-content: space-between;
}

.travelmates__icons--width {
  width: 150px;
  margin-top: 7px;
  margin-left: 50px;
  padding-left: 18px;
}

.travelmates__icons--plane {
  width: 26px;
  height: 26px;
  background-image: url("../img/plane-black.svg");
  background-repeat: no-repeat;
  background-size: 21px;
  background-position: 4px 3px;
}

.travelmates__icons--bus {
  width: 26px;
  height: 26px;
  background-image: url("../img/bus-black.svg");
  background-repeat: no-repeat;
  background-size: 17px;
  background-position: 5px 3px;
}

.travelmates__icons--bike {
  width: 26px;
  height: 26px;
  background-image: url("../img/bike-black.svg");
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: 0 4px;
}

.travelmates__icons--run {
  width: 26px;
  height: 26px;
  background-image: url("../img/run-black.svg");
  background-repeat: no-repeat;
  background-size: 21px;
  background-position: 0 3px;
}

.travelmates__icons-button {
  margin-top: 13px;
  margin-bottom: 30px;
  padding-top: 23px;
  padding-bottom: 39px;
  padding-left: 24px;
  text-decoration: none;
  width: 270px;
  height: 5px;
  border-radius: 10px;
  background-color: #ffd74b;
  background-image: url(../img/triangle-arrow-right.svg);
  background-repeat: no-repeat;
  background-position: 236px 23px;
  background-size: 11px;
  color: #1d2e5b;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
}

@media (min-width: $tablet-width) {
  .travelmates {
    border-radius: 0 0 60px 60px;
    &__wrapper{
      width: 768px;
      padding: 0 40px;
      background-image: url(../img/smolov-back-desktop@1x.png), url(../img/rogovaya-back-desktop@1x.png);
      background-position: 39px 453px,407px 107px;
      border-radius: 20px;
      &::before {
       top: 107px;
       left: 40px;
       width: 212px;
       height: 213px;
     }
   }
 }

